<template>
  <transition name="slide">
    <div class="wrapper">
      <div class="header">
        <i class="back" @click="back"></i>
        <span class="text">我的购物车</span>
      </div>
      <div class="content-wrapper">
        <scroll class="content">
          <div class="list">
            <!-- 在组件上面遍历的时候，需要加上:key，否则事件中的item取不到 https://vuejs.org/v2/guide/list.html#key -->
            <left-swipe v-for="(item,index) in list" @delete="deleteItem(item)" :key="index">
              {{index}}
            </left-swipe>
          </div>
        </scroll>
        <div class="bottom">

        </div>
      </div>
    </div>
  </transition>
</template>


<script type="text/ecmascript-6">
  import LeftSwipe from 'base/left-swipe/left-swipe'
  import Scroll from 'base/scroll/scroll'
  import {mapActions,mapGetters} from 'vuex'

  export default {
    data () {
      return {
        list: []
      }
    },
    created () {
      this._getOrderList()
    },
    methods: {
      back() {
        this.$router.back()
      },
      deleteItem(item){
        console.log(item)
      },
      _getOrderList() {
        for(let i = 0; i < 10; i++){
          this.list.push({
            id: i + 1,
            name:'wang'
          })
        }
      }
    },
    components: {
      LeftSwipe,
      Scroll
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"

  .slide-enter,.slide-leave-to
    transform translate3d(100%,0,0)

  .slide-enter-active,.slide-leave-active
    transition all 0.3s

  .wrapper
    position absolute
    top 0
    left 0
    right 0
    bottom 0
    z-index 1
    background $color-background
    .header
      position fixed
      height 44px
      top 0
      left 0
      right 0
      background $color-header-background
      display flex
      justify-content center
      align-items center
      .back
        position absolute
        background url("~common/image/back.png") no-repeat
        background-size contain
        width 8px
        height 15px
        left 15px
        top 15px
        &:before
          position absolute
          left -10px
          right -10px
          top -10px
          bottom -10px
          content ''
      .text
        color $color-header-text
        font-size $font-size-large
        font-weight bold
    .content-wrapper
      position absolute
      left 0
      right 0
      top 44px
      bottom 45px
      .content
        width 100%
        height 100%
        overflow hidden
        margin-top 10px
        .list
          background #fff
      .bottom
        position fixed
        left 0
        right 0
        bottom 0
        height 45px
        background #f0f0f0
</style>




